import React from 'react';
import styled from 'styled-components';
import {useStore} from "effector-react";
import {range} from 'lodash';

import {ThemeToggle} from "src/ui";
import {Theme} from "src/ui/theme";
import {$theme, setTheme} from "src/features/header/theme.store";
import {$zoom, setZoom} from 'src/features/header/zoom.store';
import {$absoluteGoalsPerWeek, setGoalsPerWeek} from 'src/features/goal-calendar/goals.store';

const HeaderWrapper = styled('header')`
  position: fixed;
  width: 100%;
  left: 0;
  top: -1px;
  box-sizing: border-box;
  z-index: 100;
  
  &:after {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: var(--background);
    z-index: 99;
    opacity: .85;
    transition: background-color .7s ease-in-out;
    box-sizing: border-box;
  }
`;

const HeaderContainer = styled('div')`
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: var(--max-container-width);
  margin: 0 auto;
  padding: 15px;
  position: relative;
  z-index: 100;
`;

const ZoomContainer = styled('div')`
  display: flex;
  align-items: center;
`;

const ZoomSVG = styled('svg')`
  margin-right: 7px;
  
  path {
    fill: var(--text);
    transition: fill .7s ease-in-out;
  }
`;

const GoalsCountSelect = styled('select')`
  margin-left: 20px;
  font-size: 12px;
`;

export const Header: React.FC = () => {
    const theme = useStore($theme);
    const zoom = useStore($zoom);
    const absoluteGoalsPerWeek = useStore($absoluteGoalsPerWeek);

    const handleChangeTheme = (value: boolean) => {
        value ? setTheme(Theme.light) : setTheme(Theme.dark);
    };

    return (
        <HeaderWrapper>
            <HeaderContainer>
                <ZoomContainer>
                    <ZoomPlusIcon/>
                    <input type="range" min={5} max={30} value={zoom} onChange={e => setZoom(+e.target.value)}/>
                    <GoalsCountSelect value={absoluteGoalsPerWeek} onChange={e => setGoalsPerWeek(+e.target.value)}>
                        {range(1, 8).map(index => (
                            <option value={index} key={index}>{index}</option>
                        ))}
                    </GoalsCountSelect>
                </ZoomContainer>

                <ThemeToggle
                    id="theme"
                    value={theme === Theme.light}
                    onChange={handleChangeTheme}
                />
            </HeaderContainer>
        </HeaderWrapper>
    )
};

interface IconProps {
    size?: number;
}

export const ZoomPlusIcon: React.FC<IconProps > = ({size = 15}) => {
    return (
        <ZoomSVG xmlns="http://www.w3.org/2000/svg" width={size} height={size} viewBox="0 0 192.9 192.9">
            <path
                d="M190.7 180.1l-47.07-47.08a80.81 80.81 0 0 0 18.75-51.83C162.38 36.42 125.96 0 81.2 0 36.42 0 0 36.42 0 81.2c0 44.76 36.42 81.18 81.2 81.18a80.81 80.81 0 0 0 51.82-18.75l47.08 47.08a7.47 7.47 0 0 0 10.6 0 7.5 7.5 0 0 0 0-10.6zM15 81.2C15 44.7 44.7 15 81.2 15c36.49 0 66.18 29.7 66.18 66.2 0 36.49-29.7 66.18-66.19 66.18-36.5 0-66.19-29.7-66.19-66.19z"/>
        </ZoomSVG>
    )
};
