import {createDomain} from 'effector';
import {withPersist} from 'src/common/effector-persist';

import {CalendarDate} from 'src/ui/calendar/calendar-api';
import {CalendarApi} from "src/ui/calendar";

export const GoalsDomain = createDomain();

export interface Goal {
    date: CalendarDate;
}

export const setGoalsPerWeek = GoalsDomain.event<number>();
export const addGoal = GoalsDomain.event<CalendarDate>();
export const removeGoal = GoalsDomain.event<CalendarDate>();

export const $goals = withPersist(GoalsDomain.store<Goal[]>([]), {key: 'goals'})
    .on(addGoal, (state, payload) => (
        [...state, {date: payload}])
    )
    .on(removeGoal, (state, payload) => state.filter(item => !CalendarApi.equals(item.date, payload)));

export const $absoluteGoalsPerWeek = withPersist(GoalsDomain.store<number>(4), {key: 'goals-per-week'})
    .on(setGoalsPerWeek, (state, payload) => payload);
