import {createDomain, sample} from 'effector';
import {withPersist} from 'src/common/effector-persist';

const ZoomDomain = createDomain();

export const initZoom = ZoomDomain.event();
export const setZoom = ZoomDomain.effect<number, number>()
    .use(size => {
        const html = document.getElementsByTagName('html')[0];
        html.style.fontSize = `${size}px`;

        return size;
    });

export const $zoom = withPersist(ZoomDomain.store<number>(16), {key: 'zoom'})
    .on(setZoom.done, (state, {result}) => result);

sample({
    clock: initZoom,
    source: $zoom,
    target: setZoom,
});

initZoom();
