import {createDomain, sample} from 'effector';
import {withPersist} from 'src/common/effector-persist';

import {setTheme as setT, initTheme as initT, Theme} from 'src/ui/theme';

const ThemeDomain = createDomain('theme');

export const onInitTheme = ThemeDomain.event();
export const initTheme = ThemeDomain.effect<Theme, void>()
    .use(theme => initT(theme));
export const setTheme = ThemeDomain.effect<Theme, Theme>()
    .use(params => {
        setT(params);
        return params;
    });

export const $theme = withPersist(ThemeDomain.store<Theme>(Theme.dark), {key: 'theme'})
    .on(setTheme, (state, payload) => payload);

sample({
    clock: onInitTheme,
    source: $theme,
    target: initTheme,
});

onInitTheme();
