import React from 'react';
import styled from 'styled-components';
import {useStore} from 'effector-react';
import {range} from 'lodash';
import {Calendar, CalendarApi} from 'src/ui/calendar';
import {IterableMonth} from 'src/ui/calendar/calendar-api';
import {DayComponentProps, MonthHeaderComponentProps, WeekDayComponentProps} from 'src/ui/calendar/month';
import {CalendarDay} from './calendar-day';
import {$goals, $absoluteGoalsPerWeek, addGoal, removeGoal} from 'src/features/goal-calendar/goals.store';

const Container = styled('div')`
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: var(--max-container-width);
  margin: 0 auto;
`;

const MonthTitle = styled('h2')`
  font-size: 1.3rem;
  font-family: Merriweather, sans-serif;
  color: var(--accent);
  margin: 10px 0;
`;

const YearTitle = styled('h1')`
  margin-top: 10px;
  margin-bottom: 3px;
  font-size: 3rem;
  color: var(--accent);
  font-family: Merriweather, sans-serif;
`;

const WeekDayContainer = styled('div')`
  text-align: center;
  font-size: .6rem;
  font-weight: bold;
  width: 1.3rem;
  color: var(--text);
  transition: color .7s ease-in-out;
`;

const MonthHeaderContainer = styled('div')`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const GoalsPerMonth = styled('div')`
  font-size: .8rem;
  transition: color .7s ease-in-out;
  text-align: center;
`;

const Delimiter = styled('span')`
  color: var(--accent)
`;

const DayComponent: React.FC<DayComponentProps> = ({date}) => {
    const goals = useStore($goals);
    if (date.day === 0) {
        return <CalendarDay isEmpty={true}/>
    }
    const now = new Date();
    const isCurrentDay = now.getDate() === date.day && now.getMonth() === date.month;
    const complete = goals.find(item => CalendarApi.equals(item.date, date)) !== undefined;
    const handlePress = () => {
        if (date.day > now.getDate() || date.month > now.getMonth()) {
            return;
        }

        complete ? removeGoal(date) : addGoal(date);
    };

    return (
        <CalendarDay
            isCurrentDay={isCurrentDay}
            complete={complete}
            onClick={handlePress}
        >
            {date.day}
        </CalendarDay>
    );
};

const WeekDayComponent: React.FC<WeekDayComponentProps> = ({title}) => {
    return (
        <WeekDayContainer>{title}</WeekDayContainer>
    )
};

const calcGoalsPerWeek = (goals: number, week: number[]) => Math.floor(week.filter(item => item !== 0).length / 7 * (goals - 1));
function calcAllGoalsPerMonth(goalsPerWeek: number, month: IterableMonth) {
    const [first, ..._] = month;
    const last = month[month.length - 1];
    const middle = month.slice(0, month.length - 1);

    const all = middle.length * goalsPerWeek + calcGoalsPerWeek(goalsPerWeek, first) + calcGoalsPerWeek(goalsPerWeek, last);

    if (goalsPerWeek === 4) {
        return all - 2;
    }

    if (goalsPerWeek === 3) {
        return all - 1;
    }

    return all;
}

const MonthHeader: React.FC<MonthHeaderComponentProps> = ({date}) => {
    const absoluteGoalsPerWeek = useStore($absoluteGoalsPerWeek);
    const goals = useStore($goals);
    const title = CalendarApi.getMonthNameByIndex(date.month);
    const monthDaysCount = calcAllGoalsPerMonth(absoluteGoalsPerWeek, CalendarApi.getIterableMonth(date.year, date.month));
    const goalsPerMonthCount = goals
        .filter(item => item.date.month === date.month)
        .length;
    const goalsCountVisible = date.month <= CalendarApi.getCurrentDate().month;

    return (
        <MonthHeaderContainer>
            <MonthTitle>{title}</MonthTitle>
            {goalsCountVisible && <GoalsPerMonth>{goalsPerMonthCount}/{monthDaysCount}</GoalsPerMonth>}
        </MonthHeaderContainer>
    )
};

export const GoalCalendar: React.FC = () => {
    const year = CalendarApi.getCurrentYear();
    const absoluteGoalsPerWeek = useStore($absoluteGoalsPerWeek);
    const allGoalsCount = useStore($goals).length;
    const absoluteGoalsCount = range(0, 12)
        .map(index => CalendarApi.getIterableMonth(year, index))
        .map(month => calcAllGoalsPerMonth(absoluteGoalsPerWeek, month))
        .reduce((acc, item) => acc + item, 0);
    const percentCompleteGoals = Math.floor(allGoalsCount / absoluteGoalsCount * 100);

    return (
        <Container>
            <div>
                <YearTitle>{year}</YearTitle>
                <GoalsPerMonth>{allGoalsCount}/{absoluteGoalsCount}<Delimiter>|</Delimiter>{percentCompleteGoals}%</GoalsPerMonth>
            </div>
            <Calendar
                year={year}
                WeekDayComponent={WeekDayComponent}
                DayComponent={DayComponent}
                MonthHeaderComponent={MonthHeader}
            />
        </Container>
    )
};
